import axios from 'axios';
import { OcpOnCloudReportTypePaths, } from './ocpOnCloudReports';
export function runExport(reportType, query) {
    const path = OcpOnCloudReportTypePaths[reportType];
    return axios.get(`${path}?${query}`, {
        headers: {
            Accept: 'text/csv',
        },
    });
}
