import { getQueryForWidget, getQueryForWidgetTabs, ocpOnCloudDashboardDefaultFilters, ocpOnCloudDashboardStateKey, ocpOnCloudDashboardTabFilters, } from './ocpOnCloudDashboardCommon';
export const selectOcpOnCloudDashboardState = (state) => state[ocpOnCloudDashboardStateKey];
export const selectWidgets = (state) => selectOcpOnCloudDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpOnCloudDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const defaultFilter = Object.assign({}, ocpOnCloudDashboardDefaultFilters, (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign({}, ocpOnCloudDashboardTabFilters, (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign({}, defaultFilter, { time_scope_value: -2 })),
        current: getQueryForWidget(defaultFilter),
        tabs: getQueryForWidgetTabs(widget, Object.assign({}, tabsFilter, { resolution: 'monthly' })),
    };
};
