import { getQuery, } from 'api/ocpOnCloudQuery';
export const ocpOnCloudDashboardStateKey = 'ocpOnCloudDashboard';
export const ocpOnCloudDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const ocpOnCloudDashboardTabFilters = Object.assign({}, ocpOnCloudDashboardDefaultFilters, { limit: 3 });
// Todo: cluster, project, node
export function getGroupByForTab(tab) {
    switch (tab) {
        case "projects" /* projects */:
            return { project: '*' };
        case "clusters" /* clusters */:
            return { cluster: '*' };
        case "nodes" /* nodes */:
            return { node: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(filter = ocpOnCloudDashboardDefaultFilters) {
    const query = {
        filter,
    };
    return getQuery(query);
}
export function getQueryForWidgetTabs(widget, filter = ocpOnCloudDashboardDefaultFilters) {
    const query = {
        filter,
        group_by: getGroupByForTab(widget.currentTab),
    };
    return getQuery(query);
}
