import { runExport } from 'api/ocpOnCloudExport';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchOcpOnCloudExportRequest, success: fetchOcpOnCloudExportSuccess, failure: fetchOcpOnCloudExportFailure, } = createAsyncAction('ocpOnCloudExport/request', 'ocpOnCloudExport/success', 'ocpOnCloudExport/failure')();
export function exportReport(reportType, query) {
    return (dispatch, getState) => {
        dispatch(fetchOcpOnCloudExportRequest());
        runExport(reportType, query)
            .then(res => {
            dispatch(fetchOcpOnCloudExportSuccess(res.data));
        })
            .catch(err => {
            dispatch(fetchOcpOnCloudExportFailure(err));
        });
    };
}
