import { getReportId, ocpOnCloudReportsStateKey, } from './ocpOnCloudReportsCommon';
export const selectReportsState = (state) => state[ocpOnCloudReportsStateKey];
export const selectReport = (state, reportType, query) => selectReportsState(state).byId.get(getReportId(reportType, query));
export const selectReportFetchStatus = (state, reportType, query) => selectReportsState(state).fetchStatus.get(getReportId(reportType, query));
export const selectReportError = (state, reportType, query) => selectReportsState(state).errors.get(getReportId(reportType, query));
